
$gradient : linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%);

@media (min-width : 401px){

.news-wrapper{
    margin-top:50px;
    background: $gradient;
    color:white;
    text-align: center;

    .news-info{
        margin:auto 100px;
        height: 300px;

    }


    a{
        margin-top: 20px;
        font-size: 30px;
        color: white;
        text-decoration: none;
        transition: 0.8s ease;
        border: 1px solid white;
        border-radius: 10px;
        padding-left: 30px;
        padding-right: 30px;
        
    }

    a:hover{
        color:rgb(48, 48, 182);
        background-color: white;
        font-size: 35px;
        text-decoration: underline;
    }

    p{
        font-size: 25px;
    }

   
   
}



}



@media(max-width : 400px){

.news-wrapper{
    color:white;
    background-color: rgb(37, 37, 87);


    .news-info{
        text-align: center;
        padding-top: 20px;
    }
    a{
        display: inline-block;
        width: 100px;
        margin: 10px;
        text-align: center;
        color: white;
        font-size: 30px;
        border: 1px solid white;
        text-decoration: none;
        transition: 0.5s;
        
    }

    a:hover{
        width: 110px;
    }
}

}